.page {
  display: -ms-flexbox;
  display: flex;
  width: 100vw;
  height: 100vh;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-direction: column;
      flex-direction: column;
  background-size: cover;
  background-color: #303E3F;
  -ms-flex-align: center;
      align-items: center;
}

.copyright {
  -ms-flex-item-align: start;
      align-self: flex-start;
  color: #ffffff;
  font-size: 0.8vh;
  margin: 10px;
  font-weight: bold;
}

.main-section {
  margin-top: 7vh;
  width: 100vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

.screen-shot {
  max-height: 60vh;
}

h1 {
  color: #ffffff;
  font-size: 5vh;
}

h2 {
  color: #ffffff;
  font-size: 0.8vh;
}

.btn {
  border-radius: 12px;
  color: #000000;
  font-size: 2vh;
  background: #DE9E36;
  padding: 10px 20px 10px 20px;
  text-decoration: none;
  margin-top: 10px;
  text-align: center;
}

.btn:hover {
  background: #db8300;
  text-decoration: none;
}

a {
  text-decoration: none;
}

.title {
  color: #ffffff;
  font-size: 3.5vh;
  text-align: center;
  text-shadow: 2px 2px 2px black; 
}

.subtitle {
  margin-top: -0.5vh;
  color: #ffffff;
  font-size: 2vh;
  text-align: center;
  text-shadow: 1px 1px 1px black; 
}

.android-disclaimer {
  margin-top: 2vh;
  color: #ffffff;
  font-size: 2vh;
  text-align: center;
}



