.header {
  height: 5vh;
  width: 100vw;
  position: fixed;
  top: 0;
  left: 0;
  background-color: black;
  display: -ms-flexbox;
  display: flex;
  color: white;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}

.logo {
  border-radius: 12px;
  border-color: #23ffa1;
  border-width: 2px;
  height: 6vh;
  -ms-flex-item-align: start;
      align-self: flex-start;
  margin-left: 2vh;
  margin-top: 4vh;
}

a {
  color: white;
}

a:visited {
  color: white;
}

.header-button {
  margin-right: 10px;
  font-weight: bold;
}

.button-row {
  display: -ms-flexbox;
  display: flex;
  margin-right: 10px;
}



